<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Preview') }}</h4>
                        <button type="button" class="close" v-on:click="Close()">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="table-heading-wb">
                            <div
                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 py-2 py-sm-2 py-md-4 py-lg-4 py-xl-4 float-left">
                                <div class="row">
                                    <div class="col-4 col-sm-4 col-md-4 col-lg-4 col-xl-4 float-left text-center">
                                        <img class="img-fluid" :src="blobUrl" alt="Logo" />
                                    </div>
                                    <div class="col-4 col-sm-4 col-md-4 col-lg-4 col-xl-4 float-left text-center">
                                        <img class="img-fluid"
                                            src="https://talygenv.github.io/Vue-UI-Content/images/inner/support_logo.png" />
                                    </div>
                                    <div class="col-4 col-sm-4 col-md-4 col-lg-4 col-xl-4 float-left text-center">
                                        <img class="img-fluid"
                                            src="https://talygenv.github.io/Vue-UI-Content/images/inner/jb_talygenlogo.png" />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="preview-content" id="divMainBg" style="min-height:230px;" :style="{backgroundColor: bgColor}">
                            <div class="preview-content-main">
                                <div class="preview-content-left lhtcontrol">
                                    <ul>
                                        <li class="active" style="font-weight: bold;"><a href="javascript:;"
                                                style="background: none; color: #000; cursor: auto; pointer-events:none;">Home</a>
                                            <span class="triangle" style="border-left: 25px solid #f2f2f2;" :style="{'border-left-color': bgColor,'border-left':bggColor}"></span>
                                        </li>
                                        <li><a href="javascript:;"
                                                style="color: #000; cursor: auto;  pointer-events:none">KnowledgeBase
                                                Article</a></li>
                                        <li><a href="javascript:;"
                                                style="color: #000; cursor: auto;  pointer-events:none">Post
                                                Ticket</a></li>
                                    </ul>
                                </div>
                                <div class="preview-content-right overflow-auto">
                                    <h3 class="content-heading" id="divHeaderText" style="color: #535353"
                                        v-html="IsPreviewObj.Header"  :style="{color: FColor}"></h3>
                                    <div class="content-description" id="divhometext"
                                        style="color: #535353; font-size: 18px; line-height: 24px;"
                                        v-html="IsPreviewObj.HomePageText"  :style="{color: FColor}">
                                    </div>
                                </div>
                                <div class="preview-footer" id="divFooter" v-html="IsPreviewObj.Footer"  :style="{color: FColor}">
                                  
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService';
export default {
    props: {
        IsPreviewObj: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            blobUrl: '',
            usertype: null,
            containerName: null,
            imgPath: '',
            bgColor:'',
            bggColor:'',
            FColor:''
        }
    },
    created: function () {
        this.usertype = this.GetUserInfo.UserType;
        this.containerName = this.GetUserInfo.ContainerName;        
        this.GetConfigurationData();
        this.SetStyle();
    },
    methods: {
        Close() {
            this.$parent.IsPreview = false;
        },
        GetBlobUrl: function () {
            DataService.GetBlobUrl().then((response) => {                
                var url = response.data[0].CONFIG_DATA_VALUE;
                if (this.imgPath == 'company-logo.jpg') {
                    this.blobUrl = "https://talygenv.github.io/Vue-UI-Content/images/DefaultImages/company-logo.jpg";
                    return false;
                }
                if (this.usertype == 'CA' || this.usertype == 'CL') {
                    this.blobUrl = url + '/' + this.containerName + '/' + 'Upload' + '/' + 'CompanyLogo' + '/' + this.imgPath;
                    return false;

                }
            })
        },
        GetConfigurationData: function () {
            var vm = this;
            var myObj = `moduleId=1`
            DataService.GetConfigurationData(myObj).then(function (response) {
                vm.imgPath = response.data.StorageKeys[5].CORE_CONFIG_VALUE;
                vm.GetBlobUrl();
            })
        },
        SetStyle() {
            debugger            
            if (this.IsPreviewObj.BackgroundColor.length > 0) {
                this.bgColor=this.IsPreviewObj.BackgroundColor;
                this.bggColor='25px solid ' + this.IsPreviewObj.BackgroundColor;
                $('#divMainBg').find('li').not('a').css('backgroundColor', this.IsPreviewObj.BackgroundColor);
            }
            if (this.IsPreviewObj.FontColor.length > 0) {
                this.FColor=this.IsPreviewObj.FontColor;                
            }
            $('#divhometext').css('font-size', this.IsPreviewObj.FontSize + 'px');
            var lineHeight = this.IsPreviewObj.FontSize;
            if (parseInt(lineHeight, 10) > 0) {
                if (lineHeight > 20) {
                    lineHeight = Number(lineHeight) + 10;
                }
                else {
                    lineHeight = Number(lineHeight) + 5;
                }
            }
            $('#divhometext').css('line-height', lineHeight + 'px');
            $("div.preview-content-main").height($("div.preview-content-right").height() + 60);
            $("div.preview-content-left").height($("div.preview-content-right").height() + 40);
            $("div#divMainBg").height($("div.preview-content-main").height() + 20);
            $("div#divFooter").height($("div.preview-footer").height() + 20);
        }
    }
}
</script>
<style>
.preview-container {
    max-width: 972px;
    margin: 0px auto;
    float: none;
    width: 100%;
    height: auto;
    box-shadow: 0 0 6px #000;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

.preview-header {
    width: 100%;
    background: #1c1c1c;
    float: left;
    min-height: 108px;
    border-top: 2px solid #bb0e0f;
    float: left;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.preview-header-in {
    width: 100%;
    float: left;
    padding: 20px 10px;
    height: 106px;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.preview-company-logo {
    width: 300px;
    max-width: 300px;
    height: auto;
    float: left;
    height: 68px;
}

.preview-company-logo img {
    max-height: 60px;
    max-width: 100%;
    margin-top: 4px;
}

.preview-support-logo {
    max-width: 174px;
    height: auto;
    float: left;
    padding: 12px 0px;
}

.preview-support-logo img {
    max-height: 60px;
    max-width: 100%;
}

.preview-talygen-logo {
    max-width: 155px;
    height: auto;
    float: right;
    padding: 3px 0px;
}

.preview-talygen-logo img {
    max-height: 60px;
    max-width: 100%;
}

.preview-content {
    width: 100%;
    float: left;
    padding: 10px;
    height: auto;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.preview-content-main {
    width: 100%;
    float: left;
    height: 100%;
    margin: 0px;
    padding: 0px;
}

.preview-content-left {
    float: left;
    height: 100%;
    width: 25%;
    background: rgba(256, 256, 256, 0.55);
}

.preview-content-left ul {
    width: 100%;
    margin: 0px;
    padding: 20px 0px;
    list-style: none;
}

.preview-content-left ul li.active {
    background: #f2f2f2;
}

.preview-content-left ul li {
    border-bottom: 1px solid #e6e6e6;
    display: block;
    padding: 0px;
    position: relative;
    background: #fff;
}

.preview-content-left ul li a {
    display: block;
    background: rgba(256, 256, 256, 0.2);
    padding: 0px 10px;
    text-decoration: none;
}

.preview-content-left ul li span.triangle {
    width: 0;
    height: 0;
    border-top: 25px solid transparent;
    border-bottom: 25px solid transparent;
    position: absolute;
    right: -25px;
    top: 0;
}

.preview-content-left ul li:last-child {
    border: none;
}

.preview-content-right {
    float: right;
    height: 100%;
    width: 75%;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    padding: 20px 20px 20px 50px;
    background: #fff;
}

.content-heading {
    font-size: 30px;
    margin: 0px 0px 15px 0px;
    padding: 0px 0px 15px 0px;
    border-bottom: 1px solid #ededed;
    width: 100%;
    float: left;
}

.content-heading p {
    font-size: 30px;
    margin: 0px;
    padding: 0px;
}

.content-description {
    padding: 15px;
    background: #f8f8f8;
    width: 100%;
    float: left;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.preview-footer {
    width: 100%;
    float: left;
    padding: 10px;
    height: 100%;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    background: #050505;
    color: #ffffff;
}

.preview-footer p {
    color: #6f6f6f;
    font-size: 11px;
    text-transform: uppercase;
}

.preview-footer p a {
    color: #aeaeae;
    text-decoration: none;
}

.preview-footer p a:hover {
    text-decoration: underline;
}</style>